import {IonAlert} from '@ionic/react';
import React, {useState} from 'react';
import * as FIcon from 'react-feather';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminStaff.css';
import {Page, SDataTable} from "../../components";
import {RouteComponentProps} from "react-router";
import {useApiToaster} from "../../helpers-ts";

export const AdminStaff: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const {adminStore} = rootStore
  const [showAlert, setShowAlert] = useState(false);
  const [ClienteEliminar, setClienteEliminar] = useState(null)
  const DataTable = React.createRef<any>()
  const apiToast = useApiToaster()

  const visualizar = (row: any) => {
    adminStore.setClienteDetalle(row)
    history.push('/admin/staff/detail')
  }

  const handleEditar = (row: any) => {
    adminStore.setClienteDetalle(row)
    history.push('/admin/staff/update')
  }

  const handleEliminar = (row: any) => {
    setShowAlert(true)
    setClienteEliminar(row.id)
  };

  const eliminarCliente = () => {
    const dtable = DataTable.current
    rootStore.environment.api.eliminarUsuarioStaff(ClienteEliminar, null).then((result: any) => {
      apiToast(result, 'Eliminado satisfactoriamente', 'eliminar')
      dtable.refresh()
    })
  }

  const renderAcciones = (row: any) => {
    return <div>
      <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}><FIcon.Eye size={14}/></span>
      <span className={'button ml-3'} onClick={() => {
        handleEditar(row)
      }}><FIcon.Edit size={14}/></span>
      <span className={'button ml-3'} onClick={() => {
        handleEliminar(row)
      }}><FIcon.Trash size={14}/></span>
    </div>
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <IonAlert
          isOpen={showAlert}
          onDidDismiss={() => setShowAlert(false)}
          header={'Eliminar usuario'}
          message={'Confirma que desea eliminar el usuario?'}
          buttons={[
            {
              text: 'Cancelar',
              role: 'cancel',
              cssClass: 'btn-secondary',
            },
            {
              text: 'Eliminar',
              cssClass: 'btn-primary',
              handler: () => {
                eliminarCliente();
              }
            }
          ]}
        />
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Usuarios staff</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Usuarios staff</li>
              </ol>
            </div>
          </div>
          <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item ml-auto">
              <button className="btn btn-primary d-flex align-items-center px-3" id="add-cliente" onClick={() => {
                adminStore.setClienteDetalle(null)
                history.push('/admin/staff/update')
              }}>
                Crear Usuario Staff
              </button>
            </li>
          </ul>
          <div className="card mb-0">
            <div className="card-body">
              <div className="table-responsive">
                <SDataTable
                  ref={DataTable}
                  subHeader
                  obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                    return rootStore.environment.api.usuariosStaff(page, perPage, order, search, null)
                  }}
                  columns={[
                    {
                      name: 'ID',
                      selector: 'id',
                      sortable: true,
                    },
                    {
                      name: 'Nombre',
                      selector: 'first_name',
                      sortable: true,
                    },
                    {
                      name: 'Apellido',
                      selector: 'last_name',
                      sortable: true,
                    },
                    {
                      name: 'Email',
                      selector: 'email',
                      sortable: true,
                    },
                    {
                      name: 'Acciones',
                      sortable: false,
                      cell: (row: any) => renderAcciones(row),
                    },
                  ]}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminStaff;
